.container {
	width: 100%;
	display: flex;
	height: calc(100vh - 60px);
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.img {
	margin-bottom: 20px;
	height: 105px;
	width: 100px;
}

.text {
	opacity: 0.4;
	color: #000;
	font-weight: 500;
	font-size: 16px;
}
